/* General */
body {
  background-color: #191E24;
}

h1{
  font-family:'Proxima Bold';
  font-size:60px;
  /* color:#191E24; */
  color:white;
}
h2{
  font-family:'Proxima Bold';
  font-size:60px;
  color:white;
}
h3{
  font-family:'Proxima Regular';
  color:#8C89B2;
  font-size:36px;
}

@media only screen and (max-width: 800px) {
  h1{
    font-size:48px;
  }
  h2{
    font-size:30px;
  }
}

/* Background elements */
.square{
  transform: rotate(-30deg);
  background-image: linear-gradient(60deg, #29323c 0%, #485563 100%);
  border-radius: 75px;
  height:500px;
  width:500px;
  position:absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top:600px;
  z-index: -1;

  /* box-shadow: 0 19px 38px rgba(0,0,0,0.1), 0 8px 15px rgba(0,0,0,0.1); */
  /* box-shadow: 0 19px 38px 0 rgba(0,0,0,0.15), inset 0 4px 13px 0 #282828; */
}
.circle{
  transform: rotate(-30deg);
  background: linear-gradient(90deg, #1e252b 0%, #29323b 100%);  border-radius: 500px;
  height:800px;
  width:800px;
  position:absolute;
  top:600px;
  right:800px;
  z-index: -1;
  box-shadow: 0 19px 38px rgba(0,0,0,0.1), 0 8px 15px rgba(0,0,0,0.1);

  /* box-shadow: 0 19px 38px 0 rgba(0,0,0,0.15), inset 0 4px 13px 0 #282828; */
}
.circle-alt{
  transform: rotate(-30deg);
  background: #181E23;
  border-radius: 500px;
  height:800px;
  width:800px;
  position:absolute;
  top:0px;
  right:300px;
  z-index: -1;
  box-shadow: 0 19px 38px rgba(0,0,0,0.1), 0 8px 15px rgba(0,0,0,0.1);

  /* box-shadow: 0 19px 38px 0 rgba(0,0,0,0.15), inset 0 4px 13px 0 #282828; */
}



@media only screen and (max-width: 990px) {
  .circle{
    display:none;
  }
  .square{
    display:none;
  }
}


/* Thumbnail.js */
.project{
  height:300px;
  width:100%;  
  border-radius:15px;
  margin-top:48px;
  transition: all 0.3s;
}

.project-title{
  font-family:'Colfax Bold';
  font-size: 24px;
  padding-left:36px;
  width: 100%; 
}
.project-category{
  font-family:'Colfax Light';
  font-size: 24px;
  padding-left:36px;
}
.project-img{
  height:100%;
  width:100%;
}
.project:hover{
  cursor:pointer;
  position: relative; 
  transform: translateY(-10px);
  transition: all 0.3s;
}
.project-details{
  position:relative;
  top:200px;
}

a{
  color:#979797;
  transition: all 0.1s;
  text-decoration: none;

}
a:hover{
  color:white;
  transition: all 0.1s;
  text-decoration: none;  
}

