/* Navigation */

.navigation{
  margin-top:75px;
  margin-left:100px;
  margin-right:100px;
}
.nav-text{
  font-family: "Proxima Bold";
  font-size: 24px;
  color: #817F79;
  letter-spacing: 0;
  transition: cubic-bezier(0.075, 0.82, 0.165, 1);
  transition: 0.05s;
}
.nav-text:hover{
